body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


:root{
  --black: rgb(20, 20, 20);
  --black-transparent:rgb(20, 20, 20,  0.70);
  --white: rgb(240,240,240);
  --blue: rgb(28, 145, 207);
  --red: rgb(221, 49, 49);
}



body{
  background-color: rgb(20, 20, 20);
  background-color: var(--black);
  color: rgb(240,240,240);
  color: var(--white) ;
}

.flexbox{
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
  user-select: none;
}

.flexdirection{
  flex-direction: column;
}

/* ================ Buttons =============== */
.sectionbtn{
  width: 100%;
  height: 100vh;
  flex-direction: column;
  padding-bottom: 1em;
}

.btn{
  width: 150px;
  height: 70px;
  color: rgb(20, 20, 20);
  color: var(--black);
  font-size: 1.2em;
  font-weight: 700;
  border: 0px ;
  transition: all 0.7s ease-out;
  margin-top: 2em;
  margin-bottom: 1em;
}

.btn:hover{
  background-color: rgb(240,240,240);
  background-color: var(--white);
  color: rgb(20, 20, 20);
  color: var(--black);
}

.btnclic{
    width: 100%;
    border-radius:1em ;
    background-color: rgb(28, 145, 207);
    background-color: var(--blue);
}

.btnDelete{
  width: 100%;
  border-radius:1em ;
  background-color: rgb(221, 49, 49);
  background-color: var(--red);  
}

.containerBtns{
  width: 300px;
  height: auto;
}

/* =================== DISPLAY GAME ============ */
.displayCounter{
  width: 300px;
  height: 300px;
  border: 5px solid rgb(240,240,240);
  border: 5px solid var(--white);
  border-radius: 1em;
}

.times{
  text-align: center;
  font-size: 3em;
}

.number{
  font-size: 5em;
}


/* =============== Display Inicio ============= */

.inicio{
  width: 100%;
  background-color: rgb(20, 20, 20);
  background-color: var(--black);
}

.menuinicio{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  height: 100vh;
}

.menuinicio.activate{
  position: absolute;
  top: -5000%;
}

.btn_start{
  width: 150px;
  height: 35px;
  background-color: rgb(240,240,240);
  background-color: var(--white);
  border-radius: 1em;
  border: 0px;
  font-size: medium;  
  transition: all ease-in-out .2s;
}

.btn_start:hover{
  border: 1px solid rgb(28, 145, 207);
  border: 1px solid var(--blue);
  background-color: rgb(20, 20, 20);
  background-color: var(--black);
  color: rgb(240,240,240);
  color: var(--white);
}




.sect {
    width: 100%;
    height: 100vh;
    text-align: center;
    background-color: var(--black-transparent);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    position: absolute;

}

.container__resultadosp{
    width: 70%;
    border: 1px solid var(--white);
    padding: 1em;
    border-radius: 1em;
    margin-bottom: 1em;
}


span, .leyenda{
 font-weight: 700;
}
